import React from 'react'
import { Mutation } from 'react-apollo';
import StripeCheckout from 'react-stripe-checkout';
import { STRIPE_PUBLIC_KEY } from '../config';
import gql from 'graphql-tag';

const SUBSCRIBE_USER_MUTATION = gql`
  mutation SUBSCRIBE_USER_MUTATION($token: String!, $planId: String!) {
   subscribe(token: $token, planId: $planId) {
     message
   }
  }
`;



export default class Subscribe extends React.Component {

    onToken = (token, subscribe) => {
        console.log(token);
        // fetch('/save-stripe-token', {
        //     method: 'POST',
        //     body: JSON.stringify(token),
        // }).then(response => {
        //     response.json().then(data => {
        //         alert(`We are in business, ${data.email}`);
        //     });
        // });
    }

    render() {
        let { plan: { planId, description } } = this.props;
        return (

            <Mutation
                mutation={SUBSCRIBE_USER_MUTATION}
            // refetchQueries={[{ query: CURRENT_USER_QUERY }]}
            >
                {(subscribe, { loading, error }) => (
                    <div>
                        <h2>{plan.planId}</h2>
                        <StripeCheckout
                            name={planId}
                            description={description}
                            token={(token) => this.onToken(token, subscribe)}
                            stripeKey={STRIPE_PUBLIC_KEY}
                        />
                    </div>
                )}
            </Mutation>

        )

    }
}